body {
  color: #e7e5e4;
  background-color: #000;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, sans-serif;
  overflow: hidden;
}

#scene-container {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

#ui-container {
  box-sizing: border-box;
  z-index: 20;
  background-color: #000000b3;
  width: 300px;
  height: 100%;
  padding: 20px;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translate(0%);
  box-shadow: 2px 0 5px #00000080;
}

#ui-container.collapsed {
  transform: translate(-100%);
}

@media (width >= 1024px) {
  #ui-container {
    transform: translate(0) !important;
  }
}

#panel-toggle-open {
  z-index: 150;
  color: #e5e7eb;
  cursor: pointer;
  background-color: #1f2937e6;
  border: 1px solid #4b556380;
  border-radius: .375rem;
  padding: .5rem;
  transition: all .2s;
  display: none;
  position: fixed;
  top: 1rem;
  left: 1rem;
}

#panel-toggle-open.visible {
  display: block;
}

#panel-toggle-open:hover {
  background-color: #374151e6;
  border-color: #6b7280b3;
}

@media (width >= 1024px) {
  #panel-toggle-open {
    display: none !important;
  }
}

#top-right-ui {
  z-index: 50;
  gap: .5rem;
  display: flex;
  position: fixed;
  top: 1rem;
  right: 1rem;
}

#top-right-ui button, #top-right-ui a {
  color: #e7e5e4;
  backdrop-filter: blur(10px);
  background-color: #000000b3;
  border: 1px solid #fff3;
  border-radius: .5rem;
  padding: .5rem 1rem;
  font-size: .875rem;
  text-decoration: none;
  transition: all .2s;
}

#top-right-ui button:hover, #top-right-ui a:hover {
  cursor: pointer;
  background-color: #ffffff1a;
  border-color: #ffffff4d;
}

.control-section {
  border-bottom: 1px solid #ffffff1a;
  margin-bottom: .75rem;
  padding-bottom: .5rem;
}

.control-section:last-child {
  border-bottom: none;
}

.label {
  color: #d6d3d1;
  margin-bottom: .25rem;
  font-size: .8rem;
  font-weight: 600;
  display: block;
}

.select, .input {
  color: #e7e5e4;
  background-color: #1e293bcc;
  border: 1px solid #3b82f64d;
  border-radius: .375rem;
  width: 100%;
  padding: .4rem;
  font-size: .875rem;
  transition: all .2s;
}

.select:focus, .input:focus {
  background-color: #1e293be6;
  border-color: #3b82f6;
  outline: none;
  box-shadow: 0 0 0 2px #3b82f633;
}

.control-button {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  border: none;
  border-radius: .375rem;
  margin: .1rem;
  padding: .4rem .8rem;
  font-size: .8rem;
  font-weight: 600;
  transition: all .2s;
}

.control-button:hover {
  background: linear-gradient(135deg, #2563eb, #1e40af);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #3b82f64d;
}

.action-button {
  cursor: pointer;
  border: none;
  border-radius: .375rem;
  width: 100%;
  margin: .1rem 0;
  padding: .4rem;
  font-size: .8rem;
  font-weight: 500;
  transition: all .2s;
}

.action-button.bg-green-600 {
  color: #fff;
  background: linear-gradient(135deg, #16a34a, #15803d);
}

.action-button.bg-green-600:hover {
  background: linear-gradient(135deg, #15803d, #166534);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #22c55e4d;
}

.action-button.bg-red-600 {
  color: #fff;
  background: linear-gradient(135deg, #dc2626, #b91c1c);
}

.action-button.bg-red-600:hover {
  background: linear-gradient(135deg, #b91c1c, #991b1b);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #ef44444d;
}

.action-button.bg-stone-700 {
  color: #fff;
  background: linear-gradient(135deg, #44403c, #292524);
}

.action-button.bg-stone-700:hover {
  background: linear-gradient(135deg, #292524, #1c1917);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #44403c4d;
}

.toggle-label {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  font-size: .875rem;
  display: flex;
}

.toggle-switch {
  width: 3rem;
  height: 1.5rem;
  display: inline-block;
  position: relative;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  cursor: pointer;
  background-color: #374151;
  border-radius: 1.5rem;
  transition: all .3s;
  position: absolute;
  inset: 0;
}

.toggle-slider:before {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  width: 1.125rem;
  height: 1.125rem;
  transition: all .3s;
  position: absolute;
  bottom: .1875rem;
  left: .1875rem;
}

input:checked + .toggle-slider {
  background-color: #f59e0b;
}

input:checked + .toggle-slider:before {
  transform: translate(1.5rem);
}

.info-box {
  backdrop-filter: blur(10px);
  background-color: #0009;
  border: 1px solid #fff3;
  border-radius: .5rem;
  margin-bottom: .5rem;
  padding: .75rem;
}

#zoom-indicator {
  color: #e7e5e4;
  backdrop-filter: blur(10px);
  z-index: 50;
  background-color: #000c;
  border-radius: .375rem;
  padding: .5rem;
  font-size: .75rem;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

#context-menu {
  z-index: 1000;
  backdrop-filter: blur(10px);
  background-color: #000000e6;
  border: 1px solid #fff3;
  border-radius: .375rem;
  min-width: 150px;
  padding: .5rem 0;
  display: none;
  position: absolute;
}

#context-menu div {
  cursor: pointer;
  color: #e7e5e4;
  padding: .5rem 1rem;
  font-size: .875rem;
  transition: background-color .2s;
}

#context-menu div:hover {
  background-color: #ffffff1a;
}

#help-panel {
  z-index: 200;
  backdrop-filter: blur(20px);
  background-color: #000000f2;
  border: 1px solid #fff3;
  border-radius: .75rem;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  padding: 2rem;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

#help-panel.visible {
  display: block;
}

#close-help-panel {
  color: #e7e5e4;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal-overlay {
  z-index: 200;
  background-color: #000c;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.modal {
  backdrop-filter: blur(20px);
  background-color: #000000f2;
  border: 1px solid #fff3;
  border-radius: .75rem;
  max-width: 90vw;
  max-height: 90vh;
  padding: 2rem;
  overflow-y: auto;
}

.modal-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  display: flex;
}

.close-modal {
  color: #e7e5e4;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: 1.5rem;
  display: flex;
}

.modal-action-link {
  color: #f59e0b;
  font-size: .875rem;
  text-decoration: none;
}

.modal-action-link:hover {
  text-decoration: underline;
}

.comparison-container {
  margin-bottom: 1rem;
}

.compare-image {
  border-radius: .5rem;
  max-width: 100%;
  height: auto;
}

.hidden {
  display: none;
}

h1, h2 {
  margin-bottom: .5rem !important;
}

@media (width <= 1023px) {
  #ui-container {
    width: 280px;
  }
}

#loading-overlay {
  color: #fff;
  z-index: 9999;
  background-color: #0009;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loading-content {
  background-color: #000000e6;
  border: 1px solid #ffffff26;
  border-radius: .75rem;
  padding: 1rem 1.25rem;
  font-size: 1.5rem;
  box-shadow: 0 10px 25px #0009;
}

#loading-steps {
  opacity: .85;
  margin-top: 10px;
  font-size: 11px;
}

.new-step {
  animation: .3s ease-out fadeInUp;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(6px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#app-footer {
  color: #e7e5e4;
  backdrop-filter: blur(10px);
  z-index: 50;
  background-color: #000c;
  border: 1px solid #ffffff26;
  border-radius: .375rem;
  padding: .4rem .65rem;
  font-size: .75rem;
  position: fixed;
  bottom: 1rem;
  left: 1rem;
}

#app-footer a {
  color: #f59e0b;
  text-decoration: none;
}

#app-footer a:hover {
  text-decoration: underline;
}

